.contact {
  background-color: $black;
  border-radius: 30px 30px 0 0;
  margin-top: -197px;
  z-index: -1;
  &__wrapper {
    padding-top: 180px;
    max-width: $content-max-width;
    margin: auto;
    display: flex;
    justify-content: space-between;
  }
  &__form {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    width: 480px;
  }
  &__title {
    @include titels-text;
    color: $white;
    font-size: 32px;
    margin-bottom: 56px;
  }
  &__input {
    padding: 16px 10px;
    background-color: $black;
    width: 100%;
    border: none;
    border-bottom: 1px solid $white;
    margin-bottom: 30px;
    font-family: Poppins, sans-serif;
    font-size: 15px;
    line-height: 22px;
    font-weight: 600;
    color: $white;
    opacity: 0.7;

    &:hover,
    &:focus,
    &:active {
      transition-duration: 1s;
      border: none;
      opacity: 1;
      border-bottom: 1px solid $orange;
    }
    &:invalid {
      transition-duration: 0.5s;
      border-color: $color-error;
      background-image: url("data:image/svg+xml,%3Csvg width='13px' height='13px' viewBox='0 0 30 30' version='1.1' xmlns='http://www.w3.org/2000/svg' xmlns:xlink='http://www.w3.org/1999/xlink'%3E%3Cg stroke='none' stroke-width='1' fill='none' fill-rule='evenodd'%3E%3Cg transform='translate%28-128.000000, -59.000000%29' fill='%23F44336'%3E%3Cpolygon points='157.848404 61.9920213 145.980053 73.8603723 157.848404 85.7287234 154.856383 88.7207447 142.988032 76.8523936 131.119681 88.7207447 128.12766 85.7287234 139.996011 73.8603723 128.12766 61.9920213 131.119681 59 142.988032 70.8683511 154.856383 59'%3E%3C/polygon%3E%3C/g%3E%3C/g%3E%3C/svg%3E%0A");
      background-repeat: no-repeat;
      background-position: right;
    }
  }
  &__butoon {
    margin-top: 80px;
    height: 56px;
    &:hover {
      transform: scale(1.01);
    }
  }
  &__info {
    width: 480px;
  }
  &__subtitle {
    @include subtitels-text;
    margin-bottom: 8px;
  }
  &__phone {
    display: block;
    @include contact-text;
    margin-bottom: 48px;
    &:hover {
      color: $orange;
      transition-duration: 0.5s;
    }
  }
  &__adress {
    @include contact-text;
    margin-bottom: 48px;
  }
  &__link {
    margin-right: 32px;
  }
  &__img {
    width: 24px;
    height: 24px;
    &:hover {
      background-color: $blue;
      transition-duration: 0.5s;
    }
  }
}
